.navigation {
    display: inline-flex;
    column-gap: 20px;
  
    &__link {
      position: relative;
      text-decoration: none;
      font-family: inherit;
      font-size: 18px;
      font-weight: 500;
      color: rgb(251, 251, 251);
  
      &::after {
        border-bottom: 1px solid antiquewhite;
        content: '';
        position: absolute;
        left: 0;
        bottom: -6px;
        width: 100%;
        height: 1px;
        background-color: antiquewhite;
        border-radius: 5px;
        transform: translateX(0);
        transition: transform 0.5s;
        display: none;
      }
      &:hover::after {
        transform: translateX(1);
        display: block;
      }
    }
  }